/* global.css */
html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
  background-color: #faf3e0; /* Light tan background */
  color: #333;
  padding-top: 64px; /* Space for fixed navbar */
}

.container {
  width: 80%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: underline;
}

section {
  padding: 60px 0; /* Add space between sections */
}

/* Tannish background colors for sections */
#home {
  background-color: #fff7e6; /* Light tan */
}

#about {
  background-color: #f7e1c1; /* Tan */
}

#projects {
  background-color: #e8d0a9; /* Darker tan */
}

#contact {
  background-color: #d4b483; /* Darkest tan */
}
